.ng2-c-chart__arrow {
  background: #f5f5f5;
  border-radius: 2px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  height: 18px;
  position: absolute;
  transform: rotate(45deg);
  width: 18px;
  z-index: 1; }

.ng2-c-chart__arrow--bottom {
  left: 50%;
  margin-left: -9px;
  top: -8px; }

.ng2-c-chart__arrow--top {
  bottom: -8px;
  left: 50%;
  margin-left: -9px; }

.ng2-c-chart__arrow--topleft {
  bottom: -8px;
  left: 25px;
  margin-left: -9px; }

.ng2-c-chart__arrow--topright {
  bottom: -8px;
  left: 85%;
  margin-left: -9px; }

.ng2-c-chart__arrow--left {
  bottom: -10px;
  margin-top: -9px;
  right: -8px;
  top: 50%; }

.ng2-c-chart__arrow--right {
  bottom: -10px;
  left: -8px;
  margin-top: -9px;
  top: 50%; }

.ng2-c-chart__callout {
  background: #f5f5f5;
  border-radius: 4px;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
  display: inline-block;
  left: 0;
  opacity: 0;
  pointer-events: none;
  position: absolute;
  top: 0;
  transition: opacity 0.25s linear; }

.ng2-c-chart__callout-wrapper {
  background: #f5f5f5;
  border-radius: 4px;
  color: #28648a;
  font-size: 18px;
  line-height: 1;
  padding: 10px;
  position: relative;
  white-space: nowrap;
  z-index: 2; }

.ng2-c-chart__callout--dragger {
  opacity: 1;
  top: 10px;
  transform: translateX(-25%); }

.ng2-c-chart__callout--fadein {
  opacity: 1; }

.Fng2-c-chart__callout--topleft {
  transform: translateX(-15%); }

.ng2-c-chart__callout--topright {
  transform: translateX(-85%); }

.ng2-c-chart {
  background-color: #ffffff;
  height: auto;
  position: relative;
  width: 100%; }
  .ng2-c-chart path {
    fill: none;
    stroke: #28648a;
    stroke-width: 1.5; }
    .ng2-c-chart path.area {
      stroke: none; }
    .ng2-c-chart path.domain {
      stroke-opacity: 0; }
  .ng2-c-chart .axis line,
  .ng2-c-chart .major-ticks-x line,
  .ng2-c-chart .major-ticks-y line,
  .ng2-c-chart .minor-ticks-x line,
  .ng2-c-chart .minor-ticks-y line {
    fill: none;
    stroke: #f5f5f5;
    stroke-width: 1; }
  .ng2-c-chart .axis.x,
  .ng2-c-chart .major-ticks-x.x,
  .ng2-c-chart .minor-ticks-x.x {
    fill: #28648a;
    font-size: 10px; }
    .ng2-c-chart .axis.x text,
    .ng2-c-chart .major-ticks-x.x text,
    .ng2-c-chart .minor-ticks-x.x text {
      fill: #578aa8; }
  .ng2-c-chart .axis.y,
  .ng2-c-chart .major-ticks-y.y,
  .ng2-c-chart .minor-ticks-y.y {
    fill: #9b9b9b;
    font-size: 12px; }
    .ng2-c-chart .axis.y text,
    .ng2-c-chart .major-ticks-y.y text,
    .ng2-c-chart .minor-ticks-y.y text {
      fill: #9b9b9b; }

.ng2-c-chart__selection--overlay {
  fill: none;
