/*
LineChart HTML:
<linearGradient>
    <stop class="ng2-c-chart__gradient--start" stop-opacity="0.35" offset="0%"></stop>
    <stop class="ng2-c-chart__gradient--stop" offset="100%"></stop>
</linearGradient>
The <stop> SVG element defines the colors/opacity to use on a gradient (there is no start)
Check https://developer.mozilla.org/en-US/docs/Web/SVG/Element/linearGradient for more info
*/
.ng2-c-chart__gradient--start {
  stop-color: #28648a; }

.ng2-c-chart__gradient--stop {
  stop-color: #28648a;
  stop-opacity: 0; }

.ng2-c-chart__dragger {
  fill: #c8466f; }

path.ng2-c-chart__dragger--arrow {
  fill: #ffffff;
  stroke: none; }

path.ng2-c-chart__dragger--line {
  fill: #c8466f;
  stroke: none; }
